
.sidebar h3 {
  color: #666;
}
.sidebar .recent-post {
  list-style: none;
  margin-left: 10px;
}
.sidebar .recent-post li {
    padding-left: 0;
    display: inline-block;
}
.sidebar .recent-post li {
    margin-bottom: 12px;
}
.sidebar .recent-post li a {
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-box-orient: vertical;
      word-wrap: break-word;
      -webkit-line-clamp: 2;
      font-size: 1rem;
      line-height: 1.4;
      cursor: pointer;
}
.sidebar .recent-post li p {
      margin-top: 0px;
      font-size: 0.75rem;
      color: #999;
}
.sidebar .hot-title {
  margin-top: 30px;
}
.sidebar .hot-post {
  list-style: none;
  margin-left: 10px;
}
.sidebar .hot-post li {
    padding-left: 0;
    display: inline-block;
}
.sidebar .hot-post li {
    margin-bottom: 12px;
}
.sidebar .hot-post li a {
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-box-orient: vertical;
      word-wrap: break-word;
      -webkit-line-clamp: 2;
      font-size: 1rem;
      line-height: 1.4;
      cursor: pointer;
}
.sidebar .hot-post li p {
      margin-top: 0px;
      font-size: 0.75rem;
      color: #999;
}



