
.card {
  position: relative;
  display: inline-block;
  overflow: hidden;
  width: calc(100% / 3 - 40px);
  box-sizing: border-box;
  margin-left: 30px;
  margin-bottom: 20px;
  text-align: center;
  background-size: cover;
  color: #fff;
  cursor: pointer;
}
@media only screen and (max-width: 768px) {
.card {
      width: 100%;
      margin-left: 0;
}
}
.card img {
    position: absolute;
    min-height: 100%;
    min-width: 100%;
    left: 0;
    top: 0;
    overflow: hidden;
}
.card .card-title {
    color: #fff;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    word-wrap: break-word;
    -webkit-line-clamp: 2;
}
.card .card-postCount {
    display: block;
    margin: 10px auto 20px;
}
.card .card-btn {
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translate(-50%);
    transition: transform .5s ease-in-out;
    cursor: pointer;
}
.card .mask {
    position: relative;
    padding: 40px 20px 60px;
    height: 100px;
    z-index: 20;
    background: rgba(0, 0, 0, 0.4);
}



