
.md-editor {
  z-index: 10000;
  height: 100vh;
  overflow: auto;
}
.md-editor .tag-btn {
    margin-left: 100px;
}
.md-editor .op-bar {
    z-index: 100;
    position: fixed;
    right: 100px;
    top: 8px;
}
.md-editor .post-bar {
    bottom: 0;
    width: 100%;
    height: 50px;
    z-index: 10000000;
    background: #000;
}
.md-editor .long-select {
    width: 100%;
}
.el-message {
  border-radius: 20px !important;
}



