
#write {
  z-index: 10000;
  margin-top: -100px;
}
#write .CodeMirror, #write .CodeMirror-scroll {
    min-height: calc(100% - 100px);
    min-height: 500px;
}
#write .tag-btn {
    margin-left: 100px;
}
#write .op-bar {
    z-index: 100;
    position: fixed;
    right: 100px;
    top: 8px;
}
#write .post-bar {
    bottom: 0;
    width: 100%;
    height: 50px;
    z-index: 10000000;
    background: #000;
}
#write .long-select {
    width: 100%;
}
.el-message {
  border-radius: 20px !important;
}



