
#profile {
  min-height: 1px;
}
#profile .affix-wrapper .profile-data {
    text-align: center;
    box-shadow: none;
}
#profile .affix-wrapper .profile-data .avatar-wrapper {
      margin: auto;
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background: #eee;
}
#profile .affix-wrapper .profile-data .avatar-wrapper .avatar {
        max-width: 100px;
        border-radius: 50%;
}
#profile .affix-wrapper .profile-data .name {
      font-size: 1.375rem;
}
#profile .affix-wrapper .profile-data .bio {
      line-height: 1.4;
}
#profile .affix-wrapper .profile-data .address {
      margin-bottom: 10px;
      color: #333;
      font-size: 0.875rem;
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-box-orient: vertical;
      word-wrap: break-word;
      -webkit-line-clamp: 1;
}
#profile .affix-wrapper .profile-data .social-list {
      list-style: none;
      margin-left: -20px;
      padding: 30px 0 10px;
      border-top: 1px solid #eee;
}
#profile .affix-wrapper .profile-data .social-list li {
        padding-left: 0;
        margin-left: 20px;
        display: inline-block;
}
#profile .affix-wrapper .profile-data .data {
      list-style: none;
      margin-left: -20px;
      margin-right: -20px;
      margin-top: 20px;
      border-top: 1px solid #eee;
}
#profile .affix-wrapper .profile-data .data li {
        display: inline-block;
        padding: 20px;
}
#profile .affix-wrapper .player-wrapper {
    margin-left: -10px;
    margin-top: 20px;
}
#profile .affix-wrapper .player-wrapper iframe {
      width: calc(100% + 9px);
}



