
.banner {
  position: relative;
  overflow: hidden;
  width: 100%;
  text-align: center;
  color: #fff;
}
.banner .banner-title {
    font-size: 3rem;
    color: #fff;
}
.banner .bg {
    position: absolute;
    min-width: 100%;
    left: 0;
    top: 0;
    overflow: hidden;
    height: 100%;
}
.banner .mask {
    position: relative;
    padding: 40px 20px 60px;
    height: 140px;
    z-index: 20;
    background: rgba(0, 0, 0, 0.4);
}



