
.article-typo {
  line-height: 1.6;
}
.article-typo a {
    line-height: 1.2;
}
.article-typo pre {
    overflow: auto;
    background: #eee;
    margin-top: 10px;
    margin-bottom: 16px;
    border-radius: 3px;
}
@media only screen and (min-width: 1200px) {
.article-typo pre {
        padding: 14px;
        border-radius: 6px;
}
}
.article-typo code {
    font-family: consolas, monospace;
}
.article-typo h1, .article-typo h2, .article-typo h3, .article-typo h4, .article-typo h5, .article-typo h6 {
    margin-top: 0px;
    margin-bottom: 0px;
}
.article-typo p {
    margin-top: 0px;
    margin-bottom: 15px;
}
.article-typo blockquote {
    padding-left: 0;
    margin-left: 0;
    margin-right: 0;
    position: relative;
}
.article-typo blockquote p {
      padding: 10px;
      padding-left: 10px;
      background: #eee;
      word-break: no-wrap;
}
.article-typo blockquote p:before {
        position: absolute;
        top: 0;
        left: 0px;
        display: inline-block;
        content: "";
        width: 4px;
        height: 100%;
        background: #f44336;
}
.dp-ib {
  display: inline-block;
}
.mt-s {
  margin-top: 10px;
}
.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.article-card {
  padding: 30px 30px 40px;
  margin-bottom: 30px;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12), 0px 0px 6px 0px rgba(0, 0, 0, 0.04);
  background-clip: content-box;
}
@media only screen and (max-width: 768px) {
.article-card {
      padding: 0 10px 30px;
}
}
.article-card .infobar-wrapper {
    overflow: hidden;
}
.article-card .infobar-wrapper .info-bar {
      font-size: 0.875rem;
      margin-left: -25px;
      margin-bottom: 20px;
      color: #888;
      list-style: none;
}
.article-card .infobar-wrapper .info-bar li {
        padding-left: 0;
        display: inline-block;
}
.article-card .infobar-wrapper .info-bar > li {
        margin-left: 25px;
        float: left;
}
.article-card .infobar-wrapper .info-bar > li:last-child {
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        word-wrap: break-word;
        -webkit-line-clamp: 1;
}
.article-card .infobar-wrapper .info-bar .tag-list {
        display: inline-block;
        list-style: none;
}
.article-card .infobar-wrapper .info-bar .tag-list li {
          padding-left: 0;
          display: inline-block;
}
.article-card .infobar-wrapper .info-bar.style-bar {
        background: #eee;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 15px;
}
.article-card .title {
    font-size: 2.125rem;
    display: inline-block;
    cursor: pointer;
    margin-bottom: 10px;
}
.article-card .line-clamp {
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    word-wrap: break-word;
    -webkit-line-clamp: 10;
}
.article-card .more-btn {
    display: block;
    margin: 20px auto;
}
.full-screen {
  position: fixed;
  z-index: 9999;
  top: 0px;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background: #fff;
}
.full-screen .article-box {
    margin: 0 auto;
    max-width: 768px;
    padding: 20px;
    box-shadow: 2px 2px 8px #ccc;
}



